import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/Index'

import LeftCover from '@/components/LeftCover'
import RightContent from '@/components/RightContent'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			component: Index,
			meta: {
				title: '我的vue'
			}
		},
		{
			path: '/index.html',
			component: Index,
			meta: {
				title: '首页'
			}
		},
		{
			path: '/leftcover.html',
			component: LeftCover,
			meta: {
				title: '左封面'
			}
		},
		{
			path: '/rightcontent.html',
			component: RightContent,
			meta: {
				title: '个人信息'
			}
		}

	],
	mode: 'history'
})



