<template>
	<div class="header-intro">
			<div class="intro">
				<h1>Here is my <br>resume </h1>
				<p>一个由Html+Css+Vue组成的<br>个人简历
				</p>
				<p class="header-next" @click="$_next">Next</p>
			</div>
	</div>
</template>

<script>
export default {
  name: 'HeaderIntro',
  data () {
    return {
     
    }
  },
	methods:{
		$_next:function(){
			var width=$('html').width();
			$('html').animate({ scrollTop: width>1366?'1000':'680' }, 500)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.intro{
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		color: #fff;
		text-align: center;
		padding: 8rem 4rem 6rem 4rem;
		min-height: 1000px;
		box-sizing: border-box;
	}
	.intro h1{
		font-size: 4rem;
		line-height: 1;
		margin: 0 0 2rem;
	}
	.intro p{
		font-size: 1.5rem;
		margin: 0 0 2rem;
	}
	.intro .header-next{
		margin: 0 auto;
		cursor: pointer;
		width: 60px;
		height: 60px;
		line-height: 60px;
		font-size: 1.2rem;
		color: #159de3;
		border: 1px dashed #159de3;
		border-radius: 50%;
		-webkit-animation: go 1s ease-in-out alternate infinite;
		-moz-animation: go 1s ease-in-out alternate infinite;
		animation: go 1s ease-in-out alternate infinite;
	}
	.intro .header-next:hover{
		transition: 0.3s all;
		color: #b8e7ff;
		border-color: #b8e7ff;
	}
	@keyframes go {
	   0% {
	       -webkit-transform: rotate(10deg);
	       -webkit-transform-origin: top center;
	       -moz-transform: rotate(10deg);
	       -moz-transform-origin: top center;
	       transform: rotate(10deg);
	       transform-origin: top center;
	    }
	    100% {
	       -webkit-transform: rotate(-10deg);
	       -webkit-transform-origin: top center;
	       -moz-transform: rotate(-10deg);
	       -moz-transform-origin: top center;
	       transform: rotate(-10deg);
	       transform-origin: top center;
	    }
	}
	@media screen and (max-width:1366px){
		.intro{
			min-height: 680px;
		}
	}
</style>



