<template>
	<div class="index">
		<div id="wrapper" class="wrapper">
			<div class="bg"></div>
			<header-intro></header-intro>
			<div class="container"></div>
			<!-- <left-cover></left-cover> -->
			<!-- <right-content></right-content> -->
		</div>
	</div>
</template>

<script>
import HeaderIntro from './HeaderIntro.vue'
import LeftCover from './LeftCover.vue';
import RightContent from './RightContent.vue';
export default {
	name: 'Index',
	data() {
		return {};
	},
	methods: {
	},
	computed: {},
	components: {
		HeaderIntro:HeaderIntro,
		LeftCover: LeftCover,
		RightContent: RightContent
	},
	mounted() {
	}
};
</script>

<style>
.wrapper {
	position: relative;
	width: 100%;
	height: 100%;
}
.container {
	width: 100%;
	margin: 0 auto;
	min-height: 1000px;
	/* margin-top: 50%; */
	background: rgba(255, 255, 255, 0.3);
}
.bg {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	/* transition: 0.2s all; */
	background-color: #212931;
	background-image: url('http://demo.cssmoban.com/cssthemes5/twts_197_massively/images/bg.jpg');
	background-size: auto, auto, 100% auto;
	background-position: center, center, top center;
	/* background-repeat: repeat,	no-repeat,	no-repeat; */
	/* background-attachment: scroll,	scroll,	scroll; */
	z-index: -1;
}
img {
	display: block;
}
</style>



